/*! responsive-nav.js 1.0.39 by @viljamis */

.nav-collapse {
	ul {
	margin: 0;
	padding: 0;
	width: 100%;
	display: block;
	list-style: none;
		li {
			width: 100%;
			display: block;
		}
	}
}

.js .nav-collapse {
clip: rect(0 0 0 0);
max-height: 0;
position: absolute;
display: block;
overflow: hidden;
zoom: 1;

	@include r(34) {
		position: relative;
	}

	&.closed {
		@include r(34) {
		  max-height: none;
		}
	}
}

.nav-collapse.opened {
  max-height: 9999px;
}

.nav-toggle {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  
  @include r(34) {
	  display: none; 
  }
  
}